<template>
    <div class='message-box' v-if='show' @click.self='hidden'>
        <div class='message-box-content'>
            <div class='message-box-in'>
                <div class='message-title'>天赋技能</div>
                <div class='message-contents'>
                    <div class='message_img-test' v-if='false'>
                        <div class='message_img'>
                            <p class='message_img_show'>
                                <img src="" alt="">
                                <span><em>1</em><label>/2</label></span>
                            </p>
                           
                            <p class='message_text_name'>嗜血</p>
                        </div>
                        <div class='message_text'>
                            战斗开始时，增加所有雷鸣族精灵10%上限
                        </div>
                    </div>
                    <div class='message_rest_num'>
                        <p><span>消耗</span> <img /><span>500进行天赋重置？</span></p>
                    </div>
                </div>
                <div class='message-option' v-if='false'>
                    <div class='message-study' v-if='false'>
                        <p class='message-study_title'>学习要求</p>
                        <p class='message-study_text'>护肤4等级</p>
                        <p class='message-study_text'>护肤4等级</p>
                        <p class='message-study_text'>护肤4等级</p>
                    </div>
                    <div class='message_update'>
                        <p class='message_update_text'>消耗5点天赋点</p>
                        <p class='message_update_btn'>进化</p>
                    </div>
                    
                </div>
                <div class='message_res_btn'>
                        <p>重置</p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            show:false,
        }
    },
    methods:{
        hidden(){
            this.show = false;
            console.log(888)
        },
    },
}
</script>
<style lang="less" scoped>
     @import '../../common/css/pulic.less';
     .message-box{
         width:100%;
         height:100%;
         background:rgba(0,0,0,0.6);
         position:fixed;
         top:0;
         left:0;
         z-index:8888;
         display:flex;
         justify-content: center;
         align-items: center;
         .message-box-content{
             width: 595px;
             height:auto;
            //  min-height:280px;
            //  max-height:300px;
             border:3px #b1b1b1 solid;
             .border-radius(20px,20px);
             overflow: hidden;
             .message-box-in{
                 width:100%;
                 height:auto;
                 background:#aef4fa;
                 border:3px #657277 solid;
                 .border-radius(20px,20px);
                 padding-top:17px;
                 .box-sizing(@border-box);
                .message-title{
                    width:100%;
                    height:78px;
                    text-align: center;
                    background:#62bbd7;
                    color:#feea15;
                    line-height: 78px;
                    font-size:45px;
                    font-weight: bold;
                     text-shadow: 0 1px #b77607, 1px 0 #b77607, -1px 0 #b77607, 0 -1px #b77607;
                    -o-text-shadow:0 1px #824402, 1px 0 #824402, -1px 0 #824402, 0 -2px #824402;
                    -ms-text-shadow:  0 1px #b77607, 1px 0 #b77607, -1px 0 #b77607, 0 -1px #b77607;
                    -moz-text-shadow: 0 1px #b77607, 1px 0 #b77607, -1px 0 #b77607, 0 -1px #b77607;
                    -webkit-text-shadow:  0 1px #b77607, 1px 0 #b77607, -1px 0 #b77607, 0 -1px #b77607;
                }
                .message-contents{
                    width:100%;
                    height:178px;
                    background: #2d5663;
                    .message_img-test{
                        width:100%;
                        height:100%;
                        display:flex;
                        .message_img{
                            width:130px;
                            height:100%;
                              flex-shrink: 0;
                            margin-right:25px;
                            padding:12px 0 14px 9px;
                            .box-sizing(@border-box);
                            .message_img_show{
                                display:block;
                                width:112px;
                                height:112px;
                                background:white;
                                border:5px #315d6e solid;
                                .border-radius(66px,66px);
                              
                                padding:2px;
                                .box-sizing(@border-box);
                                  overflow: hidden;
                                  position:relative;
                                img{
                                    display:block;
                                    width:100%;
                                    height:100%;
                                     .border-radius(66px,66px);
                                    background:yellow;
                                    
                                }
                                span{
                                     width:100%;
                                    height:100%;
                                    border-radius:0 0 50% 50%;
                                    background:rgba(0,0,0,0.6);
                                    position:absolute;
                                    top:70%;
                                    left:0;
                                    font-size:24px;
                                    line-height: 28px;
                                    text-align: center;
                                    em{
                                      color:white; 

                                    }
                                    label{
                                        color:#09f509;
                                    }
                                }
                            }
                            .message_text_name{
                                width:100%;
                                height:28px;
                                line-height: 28px;
                                color:white;
                                font-size:28px;
                                text-align: center;
                               
                            }
                          
                        }
                        .message_text{
                            font-size:28px;
                            color:white;
                            line-height: 51px;
                            padding:19px 0 5px 0;
                            .box-sizing(@border-box);
                        }
                    }
                    .message_rest_num{
                        width:100%;
                        height:100%;
                        display:flex;
                        justify-content: center;
                         align-items: center;
                         font-size:28px;
                         color:white;
                         text-align: center;
                         p{
                             display:flex;
                         }
                    }
                }
                .message-option{
                    width:100%;
                    height:182px;
                    background:#62bbd7;
                    .message-study{
                        width:100%;
                        height:100%;
                       padding-top:17px;
                       .box-sizing(@border-box);
                        color:#ff0000;
                        .message-study_title{
                            font-size:28px;
                            text-align: center;
                             margin-bottom:36px;
                        }
                        .message-study_text{
                            font-size:27px;
                            text-align: center;
                        }
                    }
                    .message_update{
                        width:100%;
                        height:100%;
                        padding-top:30px;
                        .box-sizing(@border-box);
                        .message_update_text{
                            width:100%;
                            text-align: center;
                            font-size:28px;
                            color:white;
                        }
                        .message_update_btn{
                            width:192px;
                            height:73px;
                            text-align: center;
                            line-height: 73px;
                            color:white;
                            font-size:30px;
                            margin:0 auto;
                            background:url('../../assets/img/home/main/button.png') 0 0 no-repeat no-repeat;
                            background-size:100% 100%; 
                            margin-top: 34px;
                              text-shadow: 0 1px #563a05, 1px 0 #563a05, -1px 0 #563a05, 0 -1px #563a05;
                            -o-text-shadow: 0 1px #563a05, 1px 0 #563a05, -1px 0 #563a05, 0 -1px #563a05;
                            -ms-text-shadow:   0 1px #563a05, 1px 0 #563a05, -1px 0 #563a05, 0 -1px #563a05;
                            -moz-text-shadow: 0 1px #563a05, 1px 0 #563a05, -1px 0 #563a05, 0 -1px #563a05;
                            -webkit-text-shadow:  0 1px #563a05, 1px 0 #563a05, -1px 0 #563a05, 0 -1px #563a05;
                        }
                    }
                }
                .message_res_btn{
                    width:100%;
                    height:105px;
                    display:flex;
                    justify-content: center;
                    align-items: center;
                    p{
                         width:192px;
                        height:73px;
                        text-align: center;
                        line-height: 73px;
                        color:white;
                        font-size:30px;
                        margin:0 auto;
                        background:url('../../assets/img/home/main/button.png') 0 0 no-repeat no-repeat;
                        background-size:100% 100%; 
                            text-shadow: 0 1px #563a05, 1px 0 #563a05, -1px 0 #563a05, 0 -1px #563a05;
                        -o-text-shadow: 0 1px #563a05, 1px 0 #563a05, -1px 0 #563a05, 0 -1px #563a05;
                        -ms-text-shadow:   0 1px #563a05, 1px 0 #563a05, -1px 0 #563a05, 0 -1px #563a05;
                        -moz-text-shadow: 0 1px #563a05, 1px 0 #563a05, -1px 0 #563a05, 0 -1px #563a05;
                        -webkit-text-shadow:  0 1px #563a05, 1px 0 #563a05, -1px 0 #563a05, 0 -1px #563a05;
                    }

                }
             }
         }
     }
</style>


