                </p>
                <div class='talent_some'>
                    <div class='talent_skills part1' @click='skillClick'>
                        <div class='talent_skills_in'>
                            <img src="" alt="">
                            <p><span>1/</span><span>5</span></p>
                        </div>
                    </div>

                    <div class='talent_skills part2' @click='skillClick'>
