                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class='slots' v-if='activeTab === 1'>
            <medal />
        </div>
       <msgBox ref='msgBox'/>
   </tabs>
</template>
<script>
import tabs  from '@/components/tab/tab';
import choseRace  from '@/components/choseRace';
import msgBox from '@/components/messageBox'
import medal  from './medal';//勋章
export default {
    name:'role',
    components:{tabs,choseRace,medal,msgBox},
    data(){
        return{
          activeTab:0,  
          choseRaceList:[
                {id:0, imgUrl:require('@/assets/img/home/sprinte/thunder_btn.png'),name:'雷族'},
                {id:1, imgUrl: require('@/assets/img/home/sprinte/wood_btn.png'),name:'木族'},
                {id:2, imgUrl: require('@/assets/img/home/sprinte/fire_btn.png'),name:'火族'},
                {id:3, imgUrl: require('@/assets/img/home/sprinte/water_btn.png'),name:'水族'},
                {id:4, imgUrl: require('@/assets/img/home/sprinte/holy_btn.png'),name:'黑曜'},
                {id:5, imgUrl: require('@/assets/img/home/sprinte/disaster_btn.png'),name:'灾曜'},
          ]
        }
    },
    methods:{
        activeChange(obj){
           this.activeTab = obj.active;
        },
        skillClick(){
            this.$refs.msgBox.show = true;
        }
    }
}
