<style lang="less" scoped>
@import '../../../common/css/pulic.less';
    .slots{
    width:100%;
    height:100%;
    position:relative;
    overflow: hidden;
    // background:rgba(0,0,0,0.2);
    .talent{
        padding-top:20px;
        padding-left:20px;
        width:100%;
        height:100%;
        position:relative;
        overflow: auto;
        .box-sizing(@border-box);
        .talent_title{
            height:42px;
            line-height: 42px;
            font-size:30px;
            color:#315c6e;
            font-weight:bold;
        }
        .talent_nums{
            height:39px;
            line-height: 39px;
            font-size:24px;
            color:#315c6e;
            display:flex;
            span{
                display:block;
                height:100%;
                width:97px;
                margin-left:22px;
                background:url('../../../assets/img/home/main/button.png') 0 0 no-repeat no-repeat;
                background-size:100% 100%;
                color:white;
                font-size:21px;
                text-align: center;
                text-shadow: 0 1px #b77607, 1px 0 #b77607, -1px 0 #b77607, 0 -1px #b77607;
                -o-text-shadow:0 1px #824402, 1px 0 #824402, -1px 0 #824402, 0 -2px #824402;
                -ms-text-shadow:  0 1px #b77607, 1px 0 #b77607, -1px 0 #b77607, 0 -1px #b77607;
                -moz-text-shadow: 0 1px #b77607, 1px 0 #b77607, -1px 0 #b77607, 0 -1px #b77607;
                -webkit-text-shadow:  0 1px #b77607, 1px 0 #b77607, -1px 0 #b77607, 0 -1px #b77607;
            }
        }
        .talent_some{
            width:588px;
            height:1003px;
            position:absolute;
            top:5px;
            left:100px;
            background:url('../../../assets/img/home/role/talent_line.png') 0 0 no-repeat no-repeat;
            background-size: 100% ;
            .talent_skills{
                width:112px;
                height:112px;
                background:white;
                border:4px #315d6e solid;
                .border-radius(50%,50%);
                padding:2px;
                .box-sizing(@border-box);
                position:absolute;
                overflow: hidden;
                .talent_skills_in{
                    width:100%;
                    height:100%;
                    position:relative;
                     img{
                        display:block;
                        width:100%;
                        height:100%;
                        background:yellow;
                        .border-radius(50%,50%);
                      
                    }
                    p{
                        width:100%;
                        height:100%;
                        position:absolute;
                        .border-radius(50%,0);
                        background:rgba(0,0,0,0.6);
                        top:73px;
                        text-align: center;
                         & :nth-child(1){
                            color:white;
                         }
                        & :nth-child(2){
                            color:#09f509;
                        }
                    }
                   
                }
               
            }
            .part1{
                top:0;
                left:232px;
            }
            .part2{
                top:144px;
                left:4px;
            }
            .part3{
                top:144px;
                right:0px;
            }
            .part4{
                top:267px;
                left:238px;
            }
            .part5{
                top:420px;
                left:4px;
            }
            .part6{
                top:420px;
                left:238px;
            }
            .part7{
                top:420px;
                right:0px;
            }

            .part8{
                top:607px;
                left:4px;
            }
            .part9{
                top:607px;
                left:238px;
            }
            .part10{
                top:607px;
                right:0px;
            }
            .part11{
                top:765px;
                left:125px;
            }
            .part12{
                top:765px;
                right:125px;
            }
        }
    }
    
}
   
</style>


