<template>
<!-- 勋章 -->
    <ul class='medal'>
        <li class='medal_li' v-for='(item,index) in list' :key='index'>
            <p class='title_name'>666</p>
            <ul class='type_list'>
                <li>
                    <div>
                        <img src="@/assets/img/home/sprinte/detail/skill_num.png" alt="">
                    </div>
                    <div>
                        <p>攻击力</p>
                        <p>+100000</p>
                    </div>
                </li>
                    <li>
                    <div>
                        <img src="@/assets/img/home/sprinte/detail/defense.png" alt="">
                    </div>
                    <div>
                        <p>物理防御</p>
                        <p>+100000</p>
                    </div>
                </li>
                    <li>
                    <div>
                        <img src="@/assets/img/home/sprinte/detail/magic.png" alt="">
                    </div>
                    <div>
                        <p>法术防御</p>
                        <p>+100000</p>
                    </div>
                </li>
            </ul>
            <p class='medal_button unload_button' v-if='index%2===0'>卸载</p>
            <p class='medal_button wear_button' v-else>穿戴</p>
        </li>
    </ul>
</template>
<script>
export default {
    data(){
       return{
           list:[1,2,3,4,5,6,7,8,9]
       }
    },
    
}
</script>
<style lang="less" scoped>
@import '../../../common/css/pulic.less';
    .medal{
        width:100%;
        height:100%;
        overflow:auto;
        padding:22px 19px 22px 19px;
        .box-sizing(@border-box);
        .medal_li{
            width:100%;
            height:240px;
            border:3px #104350 solid;
            box-shadow: 0px 8px 10px -2px  #6c7170 ;
            padding-left:20px ;
             padding-right:20px ;
            .box-sizing(@border-box);
            margin-bottom:26px;
            .title_name{
                width:318px;
                height:76px;
                font-size:26px;
                margin:0 auto;
                line-height: 76px;
                text-align: center;
                background:url('../../../assets/img/home/role/medal_title.png') 0 0 no-repeat no-repeat;
                 background-size:100% 100%;
                 color:#d9fff9;
            }
            .type_list{
                width:100%;
                height:82px;
                padding:12px 40px 11px 40px;
                 .box-sizing(@border-box);
                background:#474a49;
                .border-radius(3px,3px);
                display:flex;
                justify-content:  flex-start;
                //align-content: space-between;
                flex-wrap:wrap;
                li{
                    height:100%;
                    display:flex;
                    width:33.33%;
                    flex-shrink: 0;
                   
                    & :nth-child(1){
                        height:100%;
                        width:57px;
                        height:100%;
                        margin-right:10px;
                        img{
                            display:block;
                            width:42px;
                            height:44px;
                            margin-top:11px;
                            
                        }
                    }
                    & :nth-child(2){
                        width:153px;
                        p{
                            width:100%;
                            text-align: left;
                        }
                        & :nth-child(1){
                            height:36px;
                            line-height: 36px;
                            font-size:24px;
                            color:#94c9e2;
                        }
                        & :nth-child(2){
                            height:23px;
                            line-height: 23px;
                            font-size:19px;
                            color:#00ff06;
                        }
                    }
                }
            }
            .medal_button{
                width:154px;
                height:59px;
                margin:10px auto ;
              
                line-height: 59px;
                color:white;
                font-size:25px;
                text-align: center;
                  text-shadow: 0 2px #563a05, 2px 0 #563a05, -2px 0 #563a05, 0 -2px #563a05;
                    -o-text-shadow:   0 2px #563a05, 2px 0 #563a05, -2px 0 #563a05, 0 -2px #563a05;
                    -ms-text-shadow:    0 2px #563a05, 2px 0 #563a05, -2px 0 #563a05, 0 -2px #563a05;
                    -moz-text-shadow:    0 2px #563a05, 2px 0 #563a05, -2px 0 #563a05, 0 -2px #563a05;
                    -webkit-text-shadow:   0 2px #563a05, 2px 0 #563a05, -2px 0 #563a05, 0 -2px #563a05;
            }
            .unload_button{
                background:url('../../../assets/img/home/main/button_red.png') 0 0 no-repeat no-repeat;
                background-size:100% 100%;
            }
            .wear_button{
                background:url('../../../assets/img/home/main/button.png') 0 0 no-repeat no-repeat;
                background-size:100% 100%;
            }
           

        }
    }
</style>


