<template>
    <div class='main'>
       <div class='header'>
           <headers></headers>
       </div>
       <div class='contents'>
           <!-- <tabs/> -->
           <router-view></router-view>
       </div>
       <ul class='nav'>
           <li v-for='(item,index) in navList' :key='index' @click='navChose(item,index)' :class='[index!=navList.length?"border_right":"",navActive==index?"chose_nav":null]'>{{item.name}}</li>
       </ul>
    </div>
</template>
<script>
import headers from './header';
export default {
    components:{headers},
    data(){
        return{
            navActive:0,
            navList:[
                {id:0,name:"角色",router:'/main/role'},
                {id:1,name:"精灵",router:'/main/sprinte'},
                {id:2,name:"战斗",router:'/combat'},
                {id:3,name:"背包",router:'/main/bags'},
                {id:4,name:"交易",router:''},
            ],
        }
    },
    mounted(){
        //this.$router.push({path:this.navList[this.navActive].router});
        this.navActive = this.navList.find((item,index)=>{return item.router == this.$route.path}).id
    },
    methods:{
        navChose(item,index){
            this.navActive = item.id;
            this.$router.push({path:item.router})
        },
    },
}
</script>
<style lang="less" scoped>
@import '../../../common/css/pulic.less';
    .main{
        width:100%;
        height:100%;
        .layer-background(@backgroundColor);
        overflow: hidden;
        padding-bottom:96px;
        .box-sizing(@border-box);
        .header{
            width:100%;
            height:130px;
        }
        .contents{
            width:100%;
            //height:1110px;
            height:calc(100% - 130px)
        }
        .nav{
            width:calc(100% - 8px);
            height:85px;
            border:4px white solid;
            border-image: -webkit-linear-gradient(white,#48463a) 30 30;
            border-image: -moz-linear-gradient(white,#48463a) 30 30;
            border-image: linear-gradient(white,#48463a) 30 30;
            display:flex;
            position:fixed;
            bottom:0px;
            li{
                flex:1;
                font-size:30px;
                text-align: center;
                color:white;
                padding:2px 4px;
                .box-sizing(@border-box);
                line-height: 81px;
                background:url('../../../assets/img/home/main/nav_button.png') 0 0 no-repeat no-repeat;
               background-size:100% 100%;
            }
            .border_right{
                border-right:3px solid;
                border-image: -webkit-linear-gradient(#dadcd1,#48463a) 30 30;
                border-image: -moz-linear-gradient(#dadcd1,#48463a) 30 30;
                border-image: linear-gradient(#dadcd1,#48463a) 30 30;

            }
            .chose_nav{
                box-shadow:inset 0px 0px 10px 10px #9f7f46;
                color:#f8b344;
            }
        }
    }
</style>


